.App {
  max-width: 860px;
  margin: 20px auto;
}
button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 10px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
button:hover {
  background: #926982;
  color: #fff;
}
.card-grid {
  margin-top: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 15px;
}